/* eslint-disable arrow-body-style */
export const delay = (ms = 1500) => {
  return new Promise(resolve => setTimeout(resolve, ms));
};

export const callWithDelay = (ms, ...fns) => {
  return Promise.all([
    ...fns,
    delay(ms),
  ])
    .then(response => response.slice(0, response.length - 1));
};

export function delayedCall(fn, delay = 1500) {
  setTimeout(fn, delay);
}

export const isEmpty = obj => Object.keys(obj).length === 0;


/**
 * –í—ã–∑—ã–≤–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é —Å –æ–¥–∏–Ω–∞–∫–æ–≤—ã–º–∏ –∞—Ä–≥—É–º–µ–Ω—Ç–∞–º–∏ 1 —Ä–∞–∑
 * @param {function} fn
 */
export const cacheFunction = (fn) => {
  let cache = '';

  return (...args) => {
    const argsStr = args.join(' ');
    if (argsStr === cache) return;

    cache = argsStr;
    fn(...args);
  };
};
